
import axios from 'axios'
import routerx from './../router'
import config from '../config/config'
let baseURL = config.url
//设置默认请求头
// axios.defaults.headers['token'] = config.token
axios.interceptors.request.use(
  configs => {
    // console.log(configs)
    if (configs.method == 'post'){
      configs.data.token = localStorage.getItem('token')
    }else{
      configs.params.token = localStorage.getItem('token')
    }
    
    return configs;
  },
  error => Promise.error(error)
  )
export default {
  //get请求
  get(url, param) {
    // + '&isweb=auth'
    return new Promise((resolve, reject) => {
      axios({
        method: 'get',
        url: baseURL + '?r=' + url ,
        params: param
      }).then(res => {
        resolve(res)
      }).catch((err) => {
        reject(err)
      })
    })
  },
  //post请求
  post(url, param={}) {
    return new Promise((resolve, reject) => {
      axios({
        method: 'post',
        url: baseURL + '?r=' + url,
        data: param,
        transformRequest: [function (data) {
          let ret = ''
          for (let it in data) {
            ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
          }
          return ret
        }]
      }).then(res => {
        if (res.data.code == undefined) {
          if (res.data.status == '302') {
            window.localStorage.removeItem('token')
              routerx.push({path:'/tip',query:{tip:res.data.error?res.data.error:'Token error',type:'error'}});
          } else {
              routerx.push({path:'/tip',query:{tip:'网络故障,请重试',type:'error'}});
          }
        } else {
          resolve(res)
        }
      }).catch((err) => {
          routerx.push({path:'/tip',query:{tip:err.message,type:'error'}});
        reject(err)
      })
    })
  }
}


